import React from 'react'
import ProjectCard from './ProjectCard/ProjectCard'
import jeffreyxiao from '../../../assets/img/jeffreyxiao.jpg'
import './Projects.css'

const Projects = () => {

    return (
        <section id='projects' className='bg-cloud px-1 py-6'>
            <h1 className='fs-1 fw-bold text-center mb-3'>Projects</h1>
            <div className='projects-grid mx-auto max-w-1000px'>
                <ProjectCard img={jeffreyxiao} title='StocksRepo.com' features={[
                    'Simple and intuitive stock screener',
                    'Import selected stocks into your portfolio and use a comparison tool to gain further insights',
                    'Finance blog where I post in-depth analysis of unique investment ideas',
                    'Developed with React, NodeJS, Postgres, Nginx, Puppeteer'
                ]} />
                <ProjectCard img={jeffreyxiao} title='React-Flipkart' features={[
                    'Admin page features include managing user and admin accounts, products, categories and subcategories, and authentication',
                    'Customer page features include managing products in cart, placing orders, JWT authentication',
                    'Developed with React, NodeJS, Express, MongoDB'
                ]} />
                <ProjectCard img={jeffreyxiao} title='Sudoku Solver' features={[
                    'Implemented backtracking algorithm to solve sudoku puzzles',
                    `Allows user to play sudoku game with error count and timer, and visualise algorithm upon clicking 'solve'`,
                    'Developed with React, NodeJS, Express, MongoDB'
                ]} />
            </div>
        </section>
    )
}

export default Projects